  right: 0;
  left: 0;
  margin: 0 auto;
  overflow: hidden;
  box-shadow: 0px 10px 20px -9px rgba(0, 0, 0, 0.5);
  text-align: center;
  transition: all 0.4s;
  /* background-color: url(https://sparc.org.in/wp-content/uploads/2018/12/best-C-Programming-training-gtb-nagar-sparc-academy.jpg) center no-repeat; */
  background-color: rgba(13, 224, 94, 0.219);
  background-size: 100%;
  font-size: 20px;
}

.blog-card a {
  color: #fff;
  text-decoration: none;
  transition: all 0.2s
}

.blog-card .color-overlay {
  background: rgba(64, 84, 94, 0.5);
  width: 550px;
  height: 500px;
  position: absolute;
  z-index: 10;
  top: 0;
  left: 0;
  transition: background 0.3s cubic-bezier(0.33, 0.66, 0.66, 1);
}

.blog-card .gradient-overlay {
  background-image: linear-gradient(transparent 0%, rgba(0, 0, 0, 0.6) 21%);
  width: 550px;
  height: 500px;
  position: absolute;
  top: 350px;
  left: 0;
  z-index: 15;
}

.blog-card:hover {
  box-shadow: 0px 18px 20px -9px rgba(0, 10, 30, 0.75);
}

.blog-card:hover .card-info {
  opacity: 1;
  bottom: 33px;
}

.blog-card:hover .color-overlay {
  background: rgba(64, 64, 70, 0.8);
}

.blog-card:hover .title-content {
  margin-top: 50px
}

.title-content {
  text-align: center;
  margin: 150px 0 0 0;
  position: absolute;
  z-index: 20;
  width: 100%;
  top: 0;
  left: 0;
  transition: all 0.6s
}

.blog-card:hover h3:after {

  -webkit-animation: changeLetter 0.3s 1 linear;

          animation: changeLetter 0.3s 1 linear;
  width: 80%
}

.blog-card h3,
h1 {
  font-size: 1.9em;
  font-weight: 400;
  letter-spacing: 1px;
  font-family: 'Abril Fatface', serif;
  margin-bottom: 0;
  display: inline-block;
}

.blog-card h3 a {
  text-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
  transition: all 0.2s
}

.blog-card h3 a:hover {
  text-shadow: 0px 8px 20px rgba(0, 0, 0, 0.95);
}

